import React from "react";
import styled from "styled-components";

export type FormState = {
  salary: string;
  date: string;
};

type Props = {
  onChange: (form: FormState) => void;
  formState: FormState;
};

const Container = styled.div`
  display: flex;
  flex-direction: column;
`;

const FormContainer = styled.div`
  width: 240px;
`;

export function Form({ formState, onChange }: Props) {
  function handleChangeSalary({
    target: { value },
  }: React.ChangeEvent<HTMLInputElement>) {
    onChange({ ...formState, salary: value });
  }

  function handleChangeDate({
    target: { value },
  }: React.ChangeEvent<HTMLInputElement>) {
    onChange({ ...formState, date: value });
  }

  return (
    <Container>
      <h1>–°–ø—Ä–∞–≤–µ–¥–ª–∏–≤–∞—è –∑–∞—Ä–ø–ª–∞—Ç–∞</h1>
      <FormContainer>
        <p>–Ø –ø–æ–ª—É—á–∞—é</p>
        <input
          placeholder="20 000 —Ä—É–±."
          onChange={handleChangeSalary}
          value={formState.salary}
        />
        <p>–ü–æ—Å–ª–µ–¥–Ω–∏–π —Ä–∞–∑ –º–µ–Ω—è–ª–∞—Å—å</p>
        <input
          type="date"
          placeholder="–ö–æ–≥–¥–∞ –ø–æ—Å–ª–µ–¥–Ω–∏–π —Ä–∞–∑ –ø–æ–≤—ã—à–∞–ª–∞—Å—å?"
          value={formState.date}
          onChange={handleChangeDate}
        />
      </FormContainer>
    </Container>
  );
}
