import React from "react";
import styled from "styled-components";

const Container = styled.div`
  display: flex;
  width: 100%;
  justify-content: center;
`;

function units(num: number, cases: any) {
  num = Math.abs(num);

  var word = "";

  if (num.toString().indexOf(".") > -1) {
    word = cases.gen;
  } else {
    word =
      num % 10 === 1 && num % 100 !== 11
        ? cases.nom
        : num % 10 >= 2 && num % 10 <= 4 && (num % 100 < 10 || num % 100 >= 20)
        ? cases.gen
        : cases.plu;
  }

  return word;
}

export function Header({ salary }: any) {
  return (
    <Container>
      <h2>
        –í–∞—à–∞ —Ä–µ–∞–ª—å–Ω–∞—è –∑–∞—Ä–ø–ª–∞—Ç–∞ –Ω–∞ —Å–µ–≥–æ–¥–Ω—è {salary}{" "}
        {units(salary, { nom: "—Ä—É–±–ª—å", gen: "—Ä—É–±–ª—è", plu: "—Ä—É–±–ª–µ–π" })}
      </h2>
    </Container>
  );
}
